<h2 id="算法的定义" tabindex="-1"><a class="header-anchor" href="#算法的定义" aria-hidden="true">#</a> 算法的定义</h2>
<p>::: theorem 算法的定义
计算机的问题是一个函数，把一定的输入数据映射到相应的输出结果，这个映射的过程就是算法。
:::</p>
<h2 id="衡量算法的指标" tabindex="-1"><a class="header-anchor" href="#衡量算法的指标" aria-hidden="true">#</a> 衡量算法的指标</h2>
<ul>
<li>空间复杂度</li>
</ul>
<p>根据算法写成的程序在执行时<strong>占用存储单元的长度</strong>。这个长度往往与输入数据的规模有关。</p>
<ul>
<li>时间复杂度</li>
</ul>
<p>根据算法写成的程序在执行时<strong>耗费时间的长度</strong>。这个长度往往与输入数据的规模有关。</p>
